.modal {
  &__change {
    display: none;
    background: #FFFFFF;
    border-radius: 2px;
    width: 148px;
    box-shadow: 0px 3px 6px -4px rgba(0, 0, 0, 0.12), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 9px 28px 8px rgba(0, 0, 0, 0.05);
    position: absolute;
    right: 38px;
    top: 17px;
    &.active {
      display: block;
    }
  }

  display: none;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.28);
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  padding: 50px 16px;
  z-index: 20;

  &.active {
    display: block;
  }

  &__wrap {
    max-width: 632px;
    position: relative;
    width: 100%;
    background: #fff;
    box-shadow: 0px 3px 6px -4px rgba(0, 0, 0, 0.12), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 9px 28px 8px rgba(0, 0, 0, 0.05);
    border-radius: 2px;
    margin: auto;
  }

  &__inner {
    padding: 24px;
    position: relative;
  }

  &__title {
    display: block;
    margin-bottom: 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
  }

  &--big {
    .modal__wrap {
      max-width: 746px;
    }
  }
}