.breadcrumbs {
  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    display: flex;
    align-items: center;

    &:after {
      content:'/';
      font-weight: 500;
      font-size: 14px;
      color: rgba(0, 0, 0, 0.45);
      display: block;
      margin-left: 8px;
      margin-right: 8px;
    }

    &:last-child {
      &:after {
        content: none;
      }

      span {
        color: rgba(0, 0, 0, 0.85);
      }
    }

    span {
      color: rgba(0, 0, 0, 0.45);
      font-weight: 500;
      font-size: 14px;
    }
  }
}