.form {
    &__input {
        font-size: 14px;
        padding: 7px 12px;
        color: rgba(0, 0, 0, 0.85);
        border: 1px solid #D9D9D9;
        border-radius: 2px;
        width: 100%;

        &:focus {
            border: 1px solid #436FFD;
            box-shadow: 0px 0px 0px 2px rgba(67, 111, 253, 0.2);

            & ~ .button__search .loupe {
                fill: $main-color
            }
        }

        &::placeholder {
            color: rgba(0, 0, 0, 0.25);
        }

        &--search {
            padding-left: 31px;
        }

        &--datepicker {
            background-image: url(/img/sprites/sprite.svg#calendar);
            background-repeat: no-repeat;
            background-size: 13px 13px;
            background-position: 94% center;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            padding-right: 23px;

            @media screen and (max-width: $desctop) {
                background-position: center;
            }
        }

        &--datemonth {
            text-align: center;

            @media screen and (max-width: $desctop) {
                background-image: url(/img/sprites/sprite.svg#calendar);
                background-repeat: no-repeat;
                background-size: 13px 13px;
                background-position: center;
                height: 32px;
                border-radius: 50%;
                padding: 14px;
            }
        }

        &--range {
            padding-top: 4px;
            padding-bottom: 4px;
        }

    }

    &__datepicker {
        &-wrap {
            width: 172px;

            @media screen and (max-width: $desctop) {
                max-width: 32px;

                .form__input--datepicker {
                    padding: 5px 15px;
                }
            }
        }
    }

    &__box {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: space-between;

        @media screen and (max-width: $phone) {
            flex-direction: column;
          }
    }

    &__item {
        width: 100%;
        margin-bottom: 24px;

        &--half {
            width: 47%;

            @media screen and (max-width: $phone) {
                width: 100%;
              }
        }

        &[data-req] {
            .form__label {
                &:after {
                    content: '*';
                    vertical-align: top;
                    color: #CF1322;
                }
            }
        }
    }

    &__label {
        font-size: 12px;
        line-height: 16px;
        color: rgba(0, 0, 0, 0.65);
        display: block;
        margin-bottom: 5px;
    }
}

.search {
    &__input {
        position: relative;
        width: 100%;
    }

    &__icon {
        width: 13px;
        height: 13px;
    }
}

svg path {
    fill: inherit;
}

.select {
    &__container {
        display: flex;
        align-items: center;
    }

    &__wrap {
        width: 278px;

        &--header {
            margin-right: 16px;
        }

        &--all {
            width: 256px;
            margin-right: 24px;
        }

        @media screen and (max-width: $phone) {
            width: 248px;
          }
    }
}

.select2-results__option > span {
    display: flex;
    align-items: center;
}

.select2-selection__rendered span {
    display: flex;
    align-items: center;
}

.img-flag {
    width: 24px;
    margin-right: 6px;
}

.select2-container--default .select2-selection--single.select2-label .select2-selection__arrow b {
    border-color: $main-color transparent transparent transparent;
    border-width: 6px 6px 0 6px;
}

.select2-container .select2-selection--single {
    height: 32px;
}

.select2-container--default .select2-selection--single.select2-label .select2-selection__rendered {
    line-height: 32px;
    font-weight: bold;
    font-size: 16px;
}

.select2-container--default .select2-selection--single {
    border: 1px solid #D9D9D9;
    border-radius: 2px;
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
    height: 28px;
    right: 8px;
}

.select2-container--default.select2-container--open .select2-selection--single.select2-label .select2-selection__arrow b {
    border-color:  transparent transparent $main-color transparent;
    border-width: 0 6px 6px 6px;
}

.daterangepicker .drp-calendar.right {
    position: absolute !important;
    right: 0 !important;
    top: 0 !important;
}

.daterangepicker .drp-calendar.right tbody {
    display: none !important;
}

.daterangepicker .drp-calendar.right thead > tr:nth-child(2) {
    display: none !important;
}

.daterangepicker .drp-calendar.right th.month {
    display: none !important;
}

.daterangepicker .drp-calendar.right .calendar-table {
    background: transparent !important;
}

.daterangepicker .daterangepicker.ltr .ranges, .daterangepicker.ltr .drp-calendar {
    float: none !important;
}

.daterangepicker .drp-calendar.right .daterangepicker_input {
    position: absolute !important;
}

.daterangepicker td.active, .daterangepicker td.active:hover {
    background-color: $prm-color;
}

.daterangepicker .applyBtn {
    background-color: $prm-color;
    border-radius: 0;
}

.daterangepicker .calendar-table th, .daterangepicker .calendar-table td {
    border-radius: 0 !important;
}

.daterangepicker .cancelBtn, .daterangepicker .drp-selected {
    display: none;
}

.daterangepicker .calendar-table .next span, .daterangepicker .calendar-table .prev span {
    border-color: rgba(0, 0, 0, 0.25);
}

.daterangepicker .calendar-table table thead > tr:first-child th {
    border-bottom: 1px solid #EEEEEE;
}

.checkbox {
    &__form {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }

    &__wrap {
        display: flex;
        align-items: center;

        &-item {
            display: flex;
            align-items: center;
            margin-right: 32px;
            margin-bottom: 12px;

            &:last-child {
                margin-right: 0;
            }


            &--blue {
                .checkbox__custom:after {
                    background: #85A5FF;
                }

                .report__col-desc {
                    background: #436FFD;
                }
            }

            &--green {
                .checkbox__custom:after {
                    background: #B7EB8F;
                }
                .report__col-desc {
                    background: #7CB305;
                }
            }

            &--purple {
                .checkbox__custom:after {
                    background: #D3ADF7;
                }
                .report__col-desc {
                    background: #9254DE;
                }
            }

            &--orange {
                .checkbox__custom:after {
                    background: #FFF7E6;
                }

                .report__col-desc {
                    background: #FA8C16;
                }
            }

            &--red {
                .checkbox__custom:after {
                    background: #FFCCC7;
                }
                .report__col-desc {
                    background: #CF1322;
                }

                .report__item {
                    background: #FFCCC7;
                    color:  #CF1322;
                    padding-left: 20px;
                }
            }

            &--filter {
                margin-bottom: 0;

                @media screen and (max-width: $desctop-small) {
                    margin-right: 25px;

                    .checkbox__custom-container {
                        margin-right: 0;
                    }
                }
            }
        }
    }

    &__custom {
        border: 1px solid #D9D9D9;
        border-radius: 2px;
        display: block;
        width: 16px;
        height: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 8px;

        &-container {
            display: flex;
            align-items: center;
            cursor: pointer;
            margin-right: 8px;
        }

        &--hidden {
            display: none;

            &:checked ~ .checkbox__custom:after{
                opacity: 1;
            }
        }

        &:after {
            content: '';
            width: 8px;
            height: 8px;
            opacity: 0;
            transition: $transition;
            background: $prm-color;
        }
    }
}

.radio {

    &__container {
        margin-bottom: 24px;
    }

    &__list {
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        &-item {
            margin-right: 32px;

            &:last-child {
                margin-right: 0;
            }
        }
    }

    &__custom {
        border: 1px solid #D9D9D9;
        border-radius: 2px;
        display: block;
        min-width: 16px;
        height: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 8px;
        border-radius: 50%;

        &-container {
            display: flex;
            align-items: center;
            cursor: pointer;
        }

        &--hidden {
            display: none;

            &:checked ~ .radio__custom {
                border-color: $prm-color;
            }

            &:checked ~ .radio__custom:after{
                opacity: 1;
            }
        }

        &:after {
            content: '';
            width: 8px;
            height: 8px;
            opacity: 0;
            transition: $transition;
            border-radius: 50%;
            background: $prm-color;
        }
    }
}

.select2-container--default .select2-results__option[aria-selected=true] {
    background-color: $prm-color !important; 
    color: #fff;
}

.select2-container--default .select2-results__option--highlighted[aria-selected="true"], .select2-container--default .select2-results__option--highlighted[aria-selected] {
    background-color: $prm-color !important;
    color: #FFFFFF !important;
}

.select2-search__field {
    font-size: 14px;
    padding: 7px 12px !important;
    color: rgba(0, 0, 0, 0.85) !important;
    border: 1px solid #D9D9D9 !important;
    border-radius: 2px;
    width: 100%;
    background-image: url(/img/sprites/sprite.svg#loupe-input);
    background-size: 15px;
    background-position: 95% center;
    background-repeat: no-repeat;
}