.output {
    &__wrap {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        justify-content: space-between;
    }

    &__item {
        width: 100%;
        max-width: 48%;
        margin-bottom: 24px;
        background: #FFFFFF;
        border-radius: 6px;
        padding: 24px;

        @media screen and (max-width: $laptop-horizontal) {
            max-width: 100%;
        }

        @media screen and (max-width: $phone) {
            .radio__list-item {
                margin-bottom: 12px;
            }
        }
    }

    &__head {
        display: flex;
        align-items: flex-start;

        @media screen and (max-width: $tablet) {
            flex-wrap: wrap;
            position: relative;
            padding-top: 60px;
        }
    }

    &__img {
        width: 100%;
        max-width: 288px;

        img {
            width: 100%;
            display: block;
        }

        @media screen and (max-width: $tablet) {
            max-width: 100%;
            margin-bottom: 24px;
        }
    }

    &__info {
        width: 100%;
        padding-left: 24px;

        @media screen and (max-width: $tablet) {
            padding-left: 0;
        }
    }

    &__row {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin-bottom: 24px;

        @media screen and (max-width: $tablet) {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
        }
    }

    &__logo {
        width: 36px;
        height: 36px;

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
            display: block;
        }
    }

    &__inner {
        margin-bottom: 24px;
    }

    &__title {
        font-weight: 500;
        font-size: 16px;
        color: rgba(0, 0, 0, 0.85);
        display: block;
        margin-bottom: 8px;

        &--margin {
            margin-bottom: 24px;
        }
    }

    &__box {

        &-head {
            display: flex;
            align-items: center;
            margin-bottom: 8px;
        }

        &-status {
            display: block;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            margin-right: 6px;

            &.positive {
                background: #52C41A;
            }

            &.negative {
                background: rgba(0, 0, 0, 0.25);
            }
        }

        &-inner {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            margin-bottom: -20px;
        }

        &-item {
            width: 47%;
            margin-bottom: 18px;

            @media screen and (max-width: $tablet) {
                width: 100%;
            }
        }
    }

    &__tags {

        &-list {
            margin-top: 8px;
            display: flex;
            flex-wrap: wrap;
            margin-bottom: -8px;
        }

        &-item {
            color: rgba(0, 0, 0, 0.45);
            font-size: 12px;
            line-height: 22px;
            background: #F0F0F0;
            border-radius: 16px;
            padding: 0px 8px 2px;
            margin-right: 8px;
            margin-bottom: 8px;

            &.active {
                background: #436FFD;
                color: #fff;
            }
        }
    }

    &__tabs {
        display: flex;
        white-space: nowrap;
        align-items: center;
        overflow: auto;

        &-item {
            flex: 1;
            color: rgba(0, 0, 0, 0.85);
            border-bottom: 1px solid #F0F0F0;
        }

        &-button {
            background: transparent;
            border: none;
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            padding-left: 16px;
            padding-right: 16px;
            padding-bottom: 4px;

            &.active {
                color: $prm-color;
                border-bottom: 1px solid $prm-color;
            }
        }

        &-box {
            position: relative;
        }

        &-section {
            position: absolute;
            opacity: 0;
            visibility: hidden;
            width: 100%;
            height: 0;
            overflow: hidden;
            z-index: -1;
            top: 0;
            left: 0;
            padding: 24px 0;

            &.active {
                display: block;
                position: relative;
                z-index: 1;
                visibility: visible;
                height: unset;
                opacity: 1;
            }
        }
    }

    &__plan {
        &-wrap {
            img {
                display: block;
                max-width: 100%;
                object-fit: contain;
            }
        }

        &-chart {
            width: 100%;
            height: 180px;
        }
    }

    &__header {
        display: flex;
        margin-bottom: 24px;
        &-item {
            display: flex;
            flex-direction: column;
            margin-right: 40px;

            &:last-child {
                margin-right: 0;
            }

            &--full {
                margin-bottom: 24px;
            }
        }

        &-title {
            color: #828282;
            font-size: 10px;
            text-transform: uppercase;
            display: block;
        }

        &-calc {
            color: rgba(0, 0, 0, 0.85);
            font-weight: 500;
            font-size: 32px;
        }

        &-sum {
            display: flex;
            align-items: center;
            font-size: 14px;
            line-height: 22px;

            &.positive {
                span {
                    color: #7CB305;
                }

                svg {
                    fill: #7CB305;
                }
            }

            &.negative {
                span {
                    color: #CF1322;
                }

                svg {
                    transform-origin: center;
                    transform: rotate(180deg);
                    fill: #CF1322;
                }
            }
        }

        &-sing {
            margin-left: 7px;
        }

        @media screen and (max-width: $tablet) {
            flex-wrap: wrap;

            &-item {
                white-space: nowrap;
                margin-bottom: 12px;
            }
        }
    }

    &__container {
        &-inner {
            display: flex;
            margin-bottom: 24px;

            @media screen and (max-width: $tablet) {
                flex-wrap: wrap;
                margin-bottom: 0;
            }
        }
    }
}