.user {
  &__table {
    &-header {
      display: flex;
      align-items: center;
      background: #FAFAFA;
      mix-blend-mode: multiply;
      border-bottom: 1px solid #F0F0F0;
      padding: 0 8px;

      &-item {
        height: 56px;
        display: flex;
        align-items: center;
        flex: 1;
        padding-left: 16px;

        span {
          font-weight: 600;
          font-size: 14px;
          color: rgba(0, 0, 0, 0.85);
        }

        &--small {
          min-width: 48px;
          max-width: 48px;
        }

        &--empty {
          min-width: 64px;
          max-width: 64px;
        }
      }

      @media screen and (max-width: $laptop-vertical) {
        display: none;
      }
    }

    &-row {
      position: relative;
      display: flex;
      align-items: center;
      border-bottom: 1px solid #F0F0F0;
      padding: 0 8px;

      @media screen and (max-width: $tablet) {
        flex-direction: column;
        align-items: flex-start;
        padding-top: 16px;
        padding-bottom: 16px;
      }
    }

    &-item {
      display: flex;
      align-items: center;
      min-height: 78px;
      flex: 1;
      padding-left: 16px;
      position: relative;

      &--small {
        min-width: 48px;
        max-width: 48px;

        @media screen and (max-width: $tablet) {
          display: none;
        }
      }

      &--empty {
        min-width: 64px;
        max-width: 64px;

        @media screen and (max-width: $tablet) {
          position: absolute;
          top: 16px;
          right: 16px;
          padding: 0;
          min-width: 32px;
          max-width: 32px;
        }
      }

      @media screen and (max-width: $tablet) {
        min-height: unset;
        margin-top: 8px;

        &:first-child {
          margin-top: 0;
        }
      }
    }

    &-link {
      color: $prm-color;
    }
  }

  &__inner {
    background: #FFFFFF;
    border-radius: 6px;
    overflow: hidden;

    @media screen and (max-width: $phone) {
      border-radius: 0;
    }
  }
}