.product {

    &__block {
        margin-bottom: 24px;

        &:last-child {
            margin-bottom: 0;
        }

        @media screen and (max-width: $phone) {
            padding-left: 16px;
            padding-right: 16px;
            border-radius: 0;
        }
    }

    &__box {
        display: flex;
        flex-wrap: wrap;
        margin-left: -12px;
        margin-right: -12px;
        margin-bottom: -12px;

        @media screen and (max-width: $laptop-horizontal) {
            flex-wrap: nowrap;
        }
    }

    &__item {
        position: relative;
        width: 100%;
        max-width: calc(100%/4 - 24px);
        margin-left: 12px;
        margin-right: 12px;
        border: 1px solid #EAEAEA;
        margin-bottom: 12px;

        @media screen and (max-width: $laptop-horizontal) {
            min-width: 260px;
        }
    }

    &__img {
        width: 100%;
        height: 165px;
        border-radius: 6px 6px 0 0;
        overflow: hidden;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
        }
    }

    &__inner {
        padding: 20px 16px 14px 16px;
        border-radius: 0 0 6px 6px;
        background: #FFFFFF;
    }

    &__desc {
        display: flex;
        flex-direction: column;

        &-top {
            display: flex;
            margin-bottom: 17px;
            position: relative;
        }

        &-icon {
            min-width: 25px;
            height: 25px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 12px;
            position: relative;

            span {
                color: #FFFFFF;
                font-weight: 500;
                font-size: 14px;
            }

            &--inside {
                margin-right: 6px;
            }

            @media screen and (max-width: $laptop-horizontal) {
                position: absolute;
                top: -32px;

                &--inside {
                    position: relative;
                    top: 0;
                }
            }
        }

        &-header {
            display: flex;
            align-items: center;
            color: rgba(0, 0, 0, 0.45);
            font-size: 10px;
            text-transform: uppercase;
        }

        &-text {
            color: rgba(0, 0, 0, 0.85);
            font-weight: 500;
            font-size: 16px;

            &--bold {
                font-size: 20px;
            }

            &--inside {
                display: flex;
            }
        }
    }


    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 24px;

        &:after {
            content: '';
            display: block;
            width: 100%;
            height: 1px;
            background-color: rgba($color: #BFBFBF, $alpha: 0.6);
        }
    }

    &__icon {
        min-width: 48px;
        height: 48px;
        background-color: #ffffff;
        margin-right: 8px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__name {
        font-weight: 500;
        font-size: 20px;
        white-space: nowrap;
        margin-right: 24px;
    }

    &__row {
        display: flex;
        padding-left: 36px;
        margin-bottom: 21px;

        .product__desc {
            margin-right: 24px;

            &:last-child {
                margin-right: 0;
            }
        }

        @media screen and (max-width: $laptop-horizontal) {
            padding-left: 0;
        }
    }

    &__info {
        display: flex;
        align-items: center;
        padding-left: 36px;

        &-icon {
            margin-right: 6px;
        }

        &-text {
            color: rgba(0, 0, 0, 0.25);
            font-size: 12px;
            position: relative;
            top: 1px;
        }
        &--header {
            padding-left: 0;
        }

        @media screen and (max-width: $laptop-horizontal) {
            padding-left: 0;
        }
        
    }

    &__status {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        top: 12px;
        right: 12px;
        background: #FFFFFF;
        border-radius: 11px;
        padding: 2px 8px;

        &:before {
            content: '';
            display: block;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            margin-right: 6px;
        }

        &:after {
            content: '';
            font-size: 12px;
            line-height: 16px;
        }

        &.active {
            &:before {
                background-color: #389E0D;
            }

            &:after {
                content: 'Активен';
                color: #389E0D;
            }
        }

        &.disabled {
            &:before {
                background-color: rgba(0, 0, 0, 0.25);;
            }

            &:after {
                content: 'Неактивен';
                color: rgba(0, 0, 0, 0.25);;
            }
        }
    }
}