.link__list {
    display: flex;
    flex-direction: column;
}

* {
    box-sizing: border-box;
    outline: none;
}

body {
    font-family: 'GolosTextWebRegular';
    color: #000000;
    font-size: 14px;
    width: 100%;
    margin: auto;
    min-width: 360px;
}

a,
button {
    cursor: pointer;
}

a {
    text-decoration: none;
}


svg path {
    fill: inherit;
}

ul,
ol {
    list-style-type: none;
    padding-left: 0;
    margin: 0;
}

.common {
    &__h1 {
        font-style: normal;
        font-weight: 500;
        font-size: 32px;
        line-height: 32px;
    }

    &__h2 {
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        margin: 0;
    }

    &__h3 {
        font-weight: 500;
        font-size: 14px;
        line-height: 22px;
    }

    &__subtitle {
        font-weight: 500;
        font-size: 16px;
        line-height: 16px;

        &--uppercase {
            font-weight: normal;
            font-size: 10px;
            line-height: 24px;
            text-transform: uppercase;
        }
    }

    &__text {
        font-weight: normal;
        font-size: 14px;
        line-height: 22px;
        color: rgba(0, 0, 0, 0.85);
    }

    &__container {
        width: 100%;
        max-width: 1320px;
        margin-left: auto;
        margin-right: auto;

        @media screen and (max-width: $desctop) {
            padding-left: 60px;
            padding-right: 60px;
        }

        @media screen and (max-width: $laptop-horizontal) {
            overflow: auto;
        }

        @media screen and (max-width: $tablet) {
            padding-left: 16px;
            padding-right: 16px;
            max-width: 100%;
        }

        @media screen and (max-width: $phone) {
            padding-left: 0px;
            padding-right: 0px;
            max-width: 100%;
        }
    }

    &__section {
        padding-top: 24px;
        padding-bottom: 24px;
        background: linear-gradient(238.92deg, #F8F8F8 0%, #F0F0F0 100%);
        min-height: calc(100vh - 46px);
    }

    &__hint {
        position: relative;
        margin-left: 3px;

        &-icon {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 12px;
            height: 12px;
            border: 1px solid rgba(0, 0, 0, 0.45);
            border-radius: 50%;
            cursor: pointer;

            svg {
                width: 4px;
                height: 6px;
            }

            &:hover~.common__hint-desc {
                display: block;
            }
        }

        &-desc {
            position: absolute;
            width: 270px;
            background: #000000;
            font-size: 10px;
            line-height: 12px;
            color: #fff;
            padding: 7px;
            border-radius: 4px;
            display: none;
            text-transform: initial;
            top: 15px;
            z-index: 1;
        }
    }

    &__sort {
        display: flex;
        flex-direction: column;

        &-button {
            background: transparent;
            border: none;
            width: 8px;
            height: 9px;
            padding: 0;
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            background-image: url(/img/sprites/sprite.svg#triangle);

            &--down {
                transform-origin: center;
                transform: rotate(180deg);
            }
        }
    }
}

.block {
    &__link {
        margin-top: 16px;

        &-box {
            margin-bottom: 24px;

            &:last-child {
                margin-bottom: 0;
            }
        }

        &-title {
            font-size: 10px;
            line-height: 120%;
            color: #828282;
            text-transform: uppercase;
            display: block;
        }

        &-list {
            display: flex;
            flex-wrap: wrap;
            margin-bottom: -12px;
        }

        &-item {
            width: 100%;
            max-width: calc(100%/7);
            margin-bottom: 12px;

            @media screen and (max-width: 1366px) {
                max-width: calc(100%/5);
            }

            @media screen and (max-width: $laptop-horizontal) {
                max-width: calc(100%/4);
            }

            @media screen and (max-width: $tablet) {
                max-width: calc(100%/2);
            }
        }

        &-link {
            display: flex;
            color: rgba(0, 0, 0, 0.65);
            margin-right: 7px;
            font-size: 16px;
            align-items: center;

            &:after {
                content: '';
                display: block;
                min-width: 12px;
                height: 12px;
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                background-image: url(/img/sprites/sprite.svg#link);
                margin-left: 7px;
            }

            &:hover {
                color: rgba(0, 0, 0, 0.65);
            }

            @media screen and (max-width: $tablet) {
                font-size: 14px;
            }
        }
    }
}

.datepicker {
    width: 280px;
}

.datepicker table {
    width: 100%;
}

.datepicker table tr td span {
    font-size: 14px !important;
    line-height: 22px !important;
    text-shadow: none !important;
    height: 22px;
    border-radius: 0;
    width: 31%;
    margin-bottom: 24px;
}

.datepicker table tr td span.active:hover,
.datepicker table tr td span.active:hover:hover,
.datepicker table tr td span.active.disabled:hover,
.datepicker table tr td span.active.disabled:hover:hover,
.datepicker table tr td span.active:active,
.datepicker table tr td span.active:hover:active,
.datepicker table tr td span.active.disabled:active,
.datepicker table tr td span.active.disabled:hover:active,
.datepicker table tr td span.active.active,
.datepicker table tr td span.active:hover.active,
.datepicker table tr td span.active.disabled.active,
.datepicker table tr td span.active.disabled:hover.active,
.datepicker table tr td span.active.disabled,
.datepicker table tr td span.active:hover.disabled,
.datepicker table tr td span.active.disabled.disabled,
.datepicker table tr td span.active.disabled:hover.disabled,
.datepicker table tr td span.active[disabled],
.datepicker table tr td span.active:hover[disabled],
.datepicker table tr td span.active.disabled[disabled],
.datepicker table tr td span.active.disabled:hover[disabled] {
    background: $prm-color !important;
}

.datepicker .datepicker-switch,
.datepicker .prev,
.datepicker .next,
.datepicker tfoot tr th {
    color: rgba(0, 0, 0, 0.25);
    border-bottom: 1px solid #EEEEEE;
}

.datepicker-switch {
    color: rgba(0, 0, 0, 0.85) !important;
}

.datepicker-dropdown:before,
.datepicker-dropdown:after {
    content: none !important;
}

.link {
    &__list {
        &-col {
            width: 100%;
            max-width: calc(100%/3);
            display: flex;
            flex-direction: column;
            border-right: 1px solid #F0F0F0;
            padding-right: 25px;
            padding-left: 25px;

            &:first-child {
                padding-left: 0;
            }

            &:last-child {
                padding-right: 0;
                border-right: none;
            }

            @media screen and (max-width: $phone) {
                max-width: 100%;
                padding-right: 0;
                padding-left: 0;
                border-right: none;

                &:last-child {
                    margin-bottom: -12px;
                }
            }
        }

        &-wrap {
            display: flex;
            flex-wrap: wrap;
        }

        &-action {
            display: flex;
            justify-content: space-between;
            align-items: center;
            color: rgba(0, 0, 0, 0.85);
            font-size: 16px;

            &:after {
                content: '';
                display: block;
                width: 15px;
                height: 15px;
                background-image: url(/img/svg/link.svg);
                background-repeat: no-repeat;
                background-size: contain;
                background-position: center;
            }

            &:hover {
                color: rgba(0, 0, 0, 0.85);
            }

            span {
                display: block;
                max-width: 90%;
            }
        }

        &-item {
            margin-bottom: 12px;

            &:last-child {
                margin-bottom: 0;
            }

            @media screen and (max-width: $phone) {

                &:last-child {
                    margin-bottom: 12px;
                }
            }
        }
    }
}