.competitor {
  &__top {
    display: flex;
    justify-content: flex-end;
    padding-top: 7px;

    &-item {
      display: block;
      width: 63%;
      text-align: center;
      font-weight: 600;
      font-size: 12px;
      line-height: 22px;
    }

    @media screen and (max-width: $laptop-horizontal) {
      display: none;
    }
  }

  &__header {
    display: flex;
    align-items: center;

    &-item {
      flex: 1;
      height: 38px;
      display: flex;
      border-bottom: 1px solid #E8E8E8;
      ;
      padding-left: 20px;
      padding-right: 16px;

      &--top {
        align-items: flex-start;
        font-weight: 600;
        flex: 2.2;

        @media screen and (max-width: $laptop-horizontal) {
          flex: none;
          min-width: 200px;
          max-width: 200px;
          width: 100%;
          padding-top: 10px;
        }
      }

      &--bottom {
        align-items: flex-end;
        font-weight: 600;
        font-size: 12px;
        line-height: 22px;
        justify-content: flex-end;
        padding-bottom: 3px;

        @media screen and (max-width: 1366px) {
          flex: .5;
        }

        @media screen and (max-width: $laptop-horizontal) {
          flex: none;
          min-width: 100px;
          max-width: 100px;
          width: 100%;
          padding-top: 10px;
        }
      }
    }
  }

  &__wrap {
    background-color: #fff;
    border-radius: 6px;
    overflow: hidden;

    @media screen and (max-width: $phone) {
      border-radius: 0;
  }
  }
  
  &__inner {
    @media screen and (max-width: $laptop-horizontal) {
      overflow: auto;
    }
  }

  &__table {
    &-row {
      display: flex;
      align-items: center;
    }

    &-item {
      display: flex;
      align-items: center;
      min-height: 55px;
      padding-left: 20px;
      padding-right: 16px;
      font-size: 14px;
      border-bottom: 1px solid #E8E8E8;
      flex: 1;

      &--main {
        justify-content: flex-start;
        flex: 2.2;

        @media screen and (max-width: $laptop-horizontal) {
          flex: none;
          min-width: 200px;
          max-width: 200px;
          width: 100%;
        }
      }

      &--status {
        justify-content: flex-end;

        &.light-green {
          background: #F6FFED;
          ;
        }

        &.green {
          background: #D9F7BE;
        }

        &.pink {
          background: #FFF1F0;
        }

        &.red {
          background: #FFCCC7;
        }

        &.yellow {
          background: #FFFBE6;
        }

        &.orange {
          background: #FFE7BA;
        }

        @media screen and (max-width: 1366px) {
          flex: .5;
        }

        @media screen and (max-width: $laptop-horizontal) {
          flex: none;
          min-width: 100px;
          max-width: 100px;
          width: 100%;
        }
      }
    }
  }
}