.apartment {
  margin-top: 24px;

  &__table {
    border-radius: 6px;
    overflow: hidden;

    &-header {
      display: flex;
      align-items: center;
      background: #FAFAFA;
      mix-blend-mode: multiply;
      padding-left: 8px;
      padding-right: 8px;

      &-item {
        display: flex;
        align-items: center;
        width: 100%;
        padding-top: 17px;
        padding-bottom: 17px;
        padding-left: 16px;
        font-weight: 600;
        font-size: 14px;
        height: 56px;
        text-align: center;

        span {
          display: block;
          margin-right: 8px;
        }

        &--small {
          max-width: 48px;
          span {
            margin-right: 0;
          }
        }

        &--big {
          max-width: 475px;
          min-width: 475px;
          flex: 5;

          @media screen and (max-width: 1366px) {
            max-width: 300px;
            min-width: 300px;
          }
        }

        &--middle {
          max-width: 138px;
          min-width: 138px;
        }
      }

      @media screen and (max-width: $desctop-small) {
        display: none;
      }
    }

    &-row {
      display: flex;
      align-items: center;
      padding-left: 8px;
      padding-right: 8px;
      border-bottom: 1px solid #F0F0F0;

      @media screen and (max-width: $laptop-horizontal) {
        flex-wrap: wrap;
      }

      @media screen and (max-width: $tablet) {
        padding: 16px;
      }
    }

    &-body {
      background: #fff;

      &-item {
        display: flex;
        flex-direction: column;
        flex: 1;
        padding-top: 17px;
        padding-bottom: 17px;
        padding-left: 16px;
        font-size: 14px;
        min-height: 56px;

        @media screen and (max-width: $laptop-horizontal) {
          height: unset;
        }

        @media screen and (max-width: $tablet) {
          flex: unset;
          width: 100%;
          max-width: calc(100%/3);
          padding: 0;
          min-width: unset;
        }

        &--small {
          max-width: 48px;
          span {
            margin-right: 0;
          }

          @media screen and (max-width: $desctop-small) {
            display: none;
          }
        }

        &--big {
          max-width: 475px;
          min-width: 475px;
          flex: 5;

          @media screen and (max-width: 1366px) {
            max-width: 300px;
            min-width: 300px;
          }

          @media screen and (max-width: $laptop-horizontal) {
            max-width: 100%;
            min-width: 100%;
            padding-bottom: 0;
          }

          @media screen and (max-width: $tablet) {
            max-width: 100%;
            min-width: 100%;
          }
        }

        &--middle {
          max-width: 138px;
          min-width: 138px;
        }
      }

      &-name {
        display: none;
        font-size: 10px;
        line-height: 24px;
        color: rgba(0, 0, 0, 0.45);
        text-transform: uppercase;
  
        @media screen and (max-width: $desctop-small) {
          display: block;
        }
      }
    }

    &-inner {
      display: flex;
      align-items: center;
    }

    &-box {
      display: flex;
      flex-direction: column;

      span {
        display: block;
        margin-bottom: 4px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      @media screen and (max-width: $tablet) {
        flex-direction: row;

        span {
          margin-bottom: 0;
          margin-right: 12px;

          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
  }

  &__plan {
    min-width: 32px;
    height: 32px;
    margin-right: 10px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__link {
    color: $prm-color;
    line-height: 22px;
  }

  &__reload {
    display: none;
    padding-top: 10px;

    @media screen and (max-width: $desctop-small) {
      display: block;
    }
  }
}