.offer {
  &__inner {
    padding: 0 24px 0 24px;
    background: #FFFFFF;
    border-radius: 6px;

    @media screen and (max-width: $phone) {
      padding-left: 16px;
      padding-right: 16px;
      border-radius: 0;
  }
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  &__title {
    width: 100%;
    max-width: 566px;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    margin: 0;
  }

  &__info {
    display: flex;
    justify-content: space-between;

    &-main {
      display: flex;
      flex-direction: column;

      @media screen and (max-width: $laptop-horizontal) {
        margin-bottom: 24px;
      }
    }

    &-calc {
      font-weight: 500;
      font-size: 32px;
      color: rgba(0, 0, 0, 0.85);
      margin-bottom: 4px;

      @media screen and (max-width: $tablet) {
        font-size: 28px;
      }
    }

    &-area {
      font-weight: 500;
      font-size: 16px;
    }

    &-box {
      width: 100%;
      max-width: 73%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      margin-bottom: -24px;

      @media screen and (max-width: $laptop-horizontal) {
        max-width: 100%;
      }
    }

    &-item {
      display: flex;
      flex-direction: column;
      width: 100%;
      max-width: calc(100%/6);
      margin-bottom: 24px;

      @media screen and (max-width: 1366px) {
        max-width: calc(100%/4);
      }

      @media screen and (max-width: $tablet) {
        max-width: calc(100%/2);
      }
    }

    &-title {
      font-size: 10px;
      text-transform: uppercase;
      color: #828282;
      display: block;
      margin-bottom: 8px;
    }

    &-text {
      font-weight: 500;
      font-size: 16px;
    }

    @media screen and (max-width: $laptop-horizontal) {
      flex-direction: column;
    }
  }

  &__item {
    position: relative;
    padding-top: 24px;
    padding-bottom: 24px;
    border-bottom: 1px solid #F0F0F0;;
  }

  &__desc {
    display: flex;
    align-items: flex-start;

    &-box {
      width: 100%;
      max-width: 100%;
    }

    @media screen and (max-width: $laptop-horizontal) {
      flex-direction: column;
    }
  }

  &__text {
    font-size: 14px;
    line-height: 22px;
    max-height: unset;
    overflow: hidden;

    &-inner {
      width: 100%;
      max-width: 50%;
      padding-right: 24px;

      @media screen and (max-width: $laptop-horizontal) {
        max-width: 100%;
        margin-bottom: 24px;
      }
    }

    &.hidden {
      max-height: 719px;
    }

    p {
      margin-top: 0;
      margin-bottom: 8px;
    }
  }

  &__img {
    margin-bottom: 28px;

    &-list {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -8px -8px -8px;
    }

    &-item {
      width: 100%;
      max-width: calc(100%/4 - 16px);
      margin-right: 8px;
      margin-left: 8px;
      margin-bottom: 8px;
      position: relative;

      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
      }

      &:after {
        content: '';
        display: block;
        padding-bottom: 100%;
        width: 100%;
      }

      @media screen and (max-width: $laptop-horizontal) {
        max-width: calc(100%/6 - 16px);
      }

      @media screen and (max-width: $tablet) {
        max-width: calc(100%/2 - 16px);
      }
    }
  }

  &__mark {
    &-list {
      display: flex;
      flex-wrap: wrap;
      margin-left: -14px;
      margin-right: -14px;

      @media screen and (max-width: $tablet) {
        margin-left: 0;
        margin-right: 0;
      }
    }

    &-item {
      display: flex;
      flex-direction: column;
      width: 100%;
      max-width: calc(100%/4 - 28px);
      margin-left: 14px;
      margin-right: 14px;
      margin-bottom: 24px;

      @media screen and (max-width: 1366px) {
        max-width: calc(100%/3);
      }

      @media screen and (max-width: $laptop-horizontal) {
        max-width: calc(100%/5);
      }

      @media screen and (max-width: $tablet) {
        max-width: calc(100%/2);
        margin-left: 0;
        margin-right: 0;
      }
    }

    &-desc {
      font-size: 10px;
      text-transform: uppercase;
      display: block;
      margin-bottom: 4px;
      color: #828282;
    }

    &-status {
      border-radius: 2px;
      padding: 0px 7px;
      max-width: max-content;

      span {
        font-size: 12px;
        line-height: 20px;
      }

      &--positive {
        border: 1px solid #B7EB8F;
        background: #F6FFED;
        span {
          color: #389E0D;
        }
      }

      &--negative {
        border: 1px solid #FFA39E;
        background: #FFF1F0;
        span {
          color: #CF1322;
        }
      }
    }
  }

  &__reload {
    display: none;
    margin-bottom: 24px;

    @media screen and (max-width: $desctop) {
        display: block;
    }
  }
}