.notification-page {
  &__inner {
    display: flex;
    flex-direction: column;
    border-radius: 6px;
  }

  &__list {
    display: flex;
    flex-direction: column;
  }

  &__item {
    display: flex;
    padding: 16px 24px;
    box-shadow: inset 0px -1px 0px #F0F0F0;
    background-color: #fff;

    &:first-child,
    &:nth-child(2),
    &:nth-child(3) {
      background: #F6F9FF;
    }

    @media screen and (max-width: $tablet) {
      flex-direction: column;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: rgba(0, 0, 0, 0.85);
    display: block;
    margin-bottom: 16px;

    @media screen and (max-width: $tablet) {
      margin-bottom: 0;
    }
  }

  &__date {
    display: block;
    color: rgba(0, 0, 0, 0.45);
    @media screen and (max-width: $tablet) {
     margin-bottom: 8px;
    }
  }

  &__main {
    width: 100%;
    max-width: 400px;

    @media screen and (max-width: $tablet) {
      display: flex;
      flex-direction: column-reverse;
      max-width: 100%;
    }
  }

  &__content {
    padding-left: 24px;
    width: 100%;

    &-inner {
      p {
        margin-top: 5px;
      }

      &.hidden {

        p,
        img,
        ul,
        span {
          display: none;
        }

        p:first-child {
          display: block;
        }
      }
    }

    @media screen and (max-width: $tablet) {
      padding-left: 0;
    }
  }
}