.button {
    background: transparent;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.85);
    border: 1px solid #D9D9D9;
    box-sizing: border-box;
    box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.016);
    border-radius: 2px;
    padding: 7px 15px;

    &__group {
        display: flex;
        align-items: center;

        &--date {
            margin-right: 24px;
        }
    }

    &__search {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        background: transparent;
        border: none;
        padding: 0;
        top: 0;
        left: 11px;
        height: 100%;
    }

    &__item {
        &:first-child {
            .button--switch {
                border-radius: 2px 0 0 2px;
            }
        }

        &:last-child {
            .button--switch {
                border-radius: 0 2px 2px 0;
                border-right: 1px solid #D9D9D9;
            }
        }
    }

    &--switch {
        border-radius: 0;
        box-shadow: none;
        border-right: none;

        &.active {
            color: $prm-color;
            border: 1px solid $prm-color;
            background: #E8EFFF;
            border-right: 1px solid $prm-color !important;
        }
    }

    &__call {
        width: 16px;
        height: 16px;
        background: transparent;
        border: none;
        padding: 0;
        svg {
            display: block;
            width: 100%;
            height: 100%;
        }
    }

    &__play {
        width: 16px;
        height: 16px;
        background: transparent;
        border: none;
        padding: 0;
        svg {
            display: block;
            width: 100%;
            height: 100%;
        }
    }

    &__download {
        display: flex;
        align-items: center;
        padding: 7px 14px;
        background: transparent;
        box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.016);
        border-radius: 2px;
        border: 1px solid #D9D9D9;
        color: $main-color;
        &:before {
            content: '';
            display: block;
            background-image: url(/img/sprites/sprite.svg#download);
            width: 14px;
            height: 14px;
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            margin-right: 8px;
        }
        &-text {
            font-size: 14px;
        }

        &:hover {
            color: $main-color;
        }

        &--cut {
            width: 32px;
            height: 32px;
            border-radius: 50%;
            margin-left: 24px;
            padding: 0;
            justify-content: center;

            &:before {
                margin: 0;
            }
        }

        @media screen and (max-width: 1366px) {
            width: 32px;
            height: 32px;
            border-radius: 50%;
            padding: 0;
            justify-content: center;
            span {
                display: none;
            }

            &:before {
                margin-right: 0;
            }
        }
    }

    &__reset {
        display: flex;
        align-items: center;
        background: transparent;
        border: none;

        &-icon {
            display: block;
            width: 13px;
            height: 13px;
            margin-right: 9px;

            svg {
                display: block;
                width: 100%;
                height: 100%;

                path {
                    fill: $prm-color;
                }
            }
        }

        span {
            color: $prm-color;
            font-size: 14px;
            line-height: 22px;
        }

        &:disabled {

            span {
                color: rgba(0, 0, 0, 0.25);
            }

            .button__reset-icon {
                path {
                    fill: rgba(0, 0, 0, 0.25);
                }
            }
        }
    }

    &__wrap {
        &--switch {
            @media screen and (max-width: $desctop) {
                position: absolute;
                right: 0;
                top: 19px;
            }
        }
    }

    &__page {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #D9D9D9;
        background: transparent;
        box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.016);
        border-radius: 2px;
        padding: 7px 14px;

        &:disabled {
            background: #F5F5F5;
            border: 1px solid #D9D9D9;
            color: rgba(0, 0, 0, 0.25);

            &:before, &:after {
                opacity: .5;
            }
        }

        &--prev {
            margin-right: 16px;
            span {
                display: block;
            }

            &:before {
                content: '';
                display: block;
                width: 12px;
                height: 12px;
                background-image: url(/img/sprites/sprite.svg#arrow-left);
                background-repeat: no-repeat;
                background-size: contain;
                background-position: center;
                margin-right: 7px;
            }
        }

        &--next {
            span {
                display: block;
            }

            &:after {
                content: '';
                display: block;
                width: 12px;
                height: 12px;
                background-image: url(/img/sprites/sprite.svg#arrow-right);
                background-repeat: no-repeat;
                background-size: contain;
                background-position: center;
                margin-left: 7px;
            }
        }

        @media screen and (max-width: $laptop-horizontal) {
            width: 32px;
            width: 32px;
            padding: 0;
            height: 32px;
            span {
                display: none;
            }

            &:after {
                margin: 0;
            }
        }
    }

    &__wrap {
        display: flex;
        align-items: center;
    }

    &__open {
        display: none;
        align-items: center;
        background: transparent;
        border: none;
        padding: 0;

        &:before {
            content: '';
            display: block;
            font-size: 14px;
            color: $prm-color;
            margin-right: 5px;
        }

        &.close {
            svg {
                transform: rotate(180deg);
            }
        }

        &.open:before {
            content: 'Читать полностью'
        }

        &.close:before {
            content: 'Свернуть';
        }

        svg {
            transform-origin: center;
            fill: $prm-color;
        }

        &.show {
            display: flex;
        }
    }

    &__more {
        width: 32px;
        height: 32px;
        border: 1px solid #D9D9D9;
        box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.016);
        border-radius: 2px;
        background-color: transparent;
        background-image: url(/img/sprites/sprite.svg#more);
        background-repeat: no-repeat;
        background-size: 50%;
        background-position: center;
        transition: $transition;

        &:hover {
            border-color: $prm-color;
            background-image: url(/img/sprites/sprite.svg#more-hover);
        }
    }

    &__change {
        display: flex;
        align-items: center;
        width: 100%;
        background-color: transparent;
        border: none;
        transition: $transition;
        padding: 8px 13px;

        &:before {
            content: '';
            display: block;
            min-width: 11px;
            height: 11px;
            background-image: url(/img/sprites/sprite.svg#edit);
            background-size: contain;
            background-repeat: no-repeat;
            margin-right: 10px;
        }

        &:hover {
            background-color: #F5F5F5;
        }
    }

    &__remove {
        display: flex;
        align-items: center;
        width: 100%;
        background-color: transparent;
        border: none;
        transition: $transition;
        padding: 8px 13px;

        &:before {
            content: '';
            display: block;
            min-width: 11px;
            height: 11px;
            background-image: url(/img/sprites/sprite.svg#trash);
            background-size: contain;
            background-repeat: no-repeat;
            margin-right: 10px;
        }

        &:hover {
            background-color: #F5F5F5;
        }
    }

    &__add {
        display: flex;
        align-items: center;
        background: transparent;
        box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.016);
        border-radius: 2px;
        border: 1px solid #D9D9D9;
        padding: 5px 16px;
        transition: $transition;

        &-icon {
            width: 12px;
            height: 12px;
            margin-right: 10px;

            svg {
                display: block;
                width: 100%;
                height: 100%;

                path {
                    fill: rgba(0, 0, 0, 0.85);;
                    transition: $transition;
                }
            }
        }

        span {
            color: rgba(0, 0, 0, 0.85);
            transition: $transition;
        }

        &:hover {
            border-color: $prm-color;

            svg {
                path {
                    fill: $prm-color;
                }
            }
            span {
                color: $prm-color;
            }
        }

        @media screen and (max-width: $laptop-horizontal) {
            width: 32px;
            height: 32px;
            border-radius: 50%;
            background-repeat: no-repeat;
            background-position: center;
            background-size: 12px;
            background-image: url(/img/sprites/sprite.svg#add-user-mobile);

            &-icon {
                display: none;
            }
            span {
                display: none;
            }
        }
    }

    &__close {
        position: absolute;
        top: 24px;
        right: 24px;
        width: 17px;
        height: 18px;
        background-image: url(/img/sprites/sprite.svg#close);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        background-color: transparent;
        border: none;
    }

    &__generate {
        background: transparent;
        border: none;
        padding: 0;
        color: $prm-color;
        font-size: 14px;
        line-height: 22px;
        margin-top: 5px;
    }

    &__submit {
        background: $prm-color;
        color: #fff;
        font-size: 14px;
        line-height: 22px;
        padding: 5px 16px;
        border: none;
        max-width: max-content;
        width: 100%;
        &-wrap {
            display: flex;
            justify-content: flex-end;
        }

        @media screen and (max-width: $phone) {
           max-width: 100%;
          }
    }

    &__link {
        display: flex;
        align-items: center;
        &-cut {
            display: block;
            width: 11px;
            height: 11px;
            margin-left: 6px;
            background-image: url(/img/svg/link-prm.svg);

            &[href=""] {
                background-image: url(/img/svg/link.svg);
            }

        }
    }

    &__list {
        background: transparent;
        width: 13px;
        height: 16px;
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
        margin-left: 8px;
        border: none;
        background-image: url(/img/svg/list.svg);
    }

    &__report {
        font-size: 14px;
        line-height: 22px; 
        color: rgba(0, 0, 0, 0.85);
        display: block;
        padding: 1px 8px;
        box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.016);
        border-radius: 2px;
        border: 1px solid #D9D9D9;

        &:hover {
            color: rgba(0, 0, 0, 0.85);
        }
    }

    &__menu {
        display: none;
        position: relative;
        min-width: 32px;
        height: 32px; 
        border: 1px solid #D9D9D9;
        box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.016);
        border-radius: 2px;
        background-image: url(/img/sprites/sprite.svg#menu);
        background-repeat: no-repeat;
        background-position: center;
        background-size: 14px;
        background-color: transparent;
        margin-right: 12px;

        &--notification:after {
            content: '';
            position: absolute;
            display: block;
            background: #FF4D4F;
            border-radius: 50%;
            width: 6px;
            height: 6px;
            top: 6px;
            right: 6px;
        }

        @media screen and (max-width: $tablet) {
            display: block;
        }
    }

    &__filter {
        background: transparent;
        border: 1px solid #D9D9D9;
        margin-right: 12px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 5px 0px;
        width: 165px;

        &:last-child {
            margin-right: 0;
        }

        &::before {
            content: '';
            display: block;
            margin-right: 9px;
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
        }

        &--filter {
            &:before {
                width: 13px;
                height: 12px;
                background-image: url(/img/sprites/sprite.svg#filter);
            }
        }

        &--sort {
            &:before {
                width: 13px;
                height: 12px;
                background-image: url(/img/sprites/sprite.svg#sort);
            }
        }
    }
}