.header {
    background: #FFFFFF;
    box-shadow: 0px 1px 0px #D9D9D9;

    &__inner {
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        @media screen and (max-width: $laptop-horizontal) {
            margin-bottom: 12px;
        }
    }

    &__title {
        font-weight: 500;
        font-size: 14px;
        line-height: 22px;
        color: rgba(0, 0, 0, 0.85);
    }

    &__container {
        padding: 12px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        overflow: hidden;

        @media screen and (max-width: $laptop-horizontal) {
            flex-direction: column;
            align-items: flex-start;
        }

        @media screen and (max-width: $phone) {
            padding-left: 16px;
            padding-right: 16px;
        }
    }

    &__group {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }

    &__search {
        width: 244px;
        margin-right: 15px;

        @media screen and (max-width: $tablet) {
            display: none;
        }
    }

    &--call {
        @media screen and (max-width: $tablet) {
            .button__download--cut {
                position: absolute;
                top: 15px;
                right: 15px;
                margin: 0;
            }

            .header__inner {
                flex-wrap: wrap;
            }

            .breadcrumbs {
                width: 100%;
                margin-top: 12px;
                margin-bottom: -6px;
            }

            .select__wrap {
                width: 190px;
            }

            .select__wrap--all {
                width: 100%;
                margin-bottom: 12px;
                margin-right: 0;
            }

            .header__group {
                width: 100%;
                justify-content: space-between;
            }

            .button--switch {
                padding-left: 12px;
                padding-right: 12px;
            }
        }
    }

    &--output {
        @media screen and (max-width: $tablet) {
            .breadcrumbs {
                width: 100%;
                margin-top: 12px;
                margin-bottom: -6px;
            }

            .form__datepicker-wrap {
                position: absolute;
                top: 12px;
                right: 15px;
            }
         }
    }

    &--competitor {
        @media screen and (max-width: $tablet) {
            .button__download {
                position: absolute;
                top: 12px;
                right: 13px;
            }

            .header__inner {
                margin-bottom: 0;
            }

            .breadcrumbs {
                width: 100%;
                margin-top: 16px;
            }
        }
    }

    &--reload {
        @media screen and (max-width: $desctop) {
            .product__info {
                display: none;
            }
        }

        @media screen and (max-width: $laptop-horizontal) {
            .breadcrumbs {
                margin-top: 12px;
                white-space: nowrap;
                text-overflow: ellipsis;
                width: 100%;
                overflow: hidden;
            }
        }
    }

    &--add {
        @media screen and (max-width: $laptop-horizontal) {
            .header__container {
                flex-direction: row;
            }
            .breadcrumbs {
                margin-top: 12px;
                white-space: nowrap;
                text-overflow: ellipsis;
                width: 100%;
                overflow: hidden;
            }
        }
        
    }
}