.scroll {
  &__box {
    padding-left: 0;

    &-container {
      width: 100%;
      padding: 16px 0;

      &.swiper-container-initialized {
        &:after {
          content: '';
          position: absolute;
          right: 0;
          display: block;
          height: 100%;
          width: 160px;
          background: linear-gradient(270deg, #FFFFFF 19.38%, rgba(255, 255, 255, 0) 100%);
          top: 1px;
          z-index: 1;
        }
      }

      &--visible {
        display: none;
      }

      @media screen and (max-width: $laptop-horizontal) {
        display: none;

        &--visible {
          display: block;
        }
      }

      
    @media screen and (max-width: $phone) {
      padding-left: 16px;
      padding-right: 16px;
  }
    }

    &-item {
        white-space: nowrap;
    }

    &-text {
      font-weight: 500;
      font-size: 16px;
      line-height: 16px;
    }

    &-button {
      display: block;
      padding-bottom: 8px;
      border-bottom: 2px solid transparent;
      color: rgba(0, 0, 0, 0.85);
      transition: $transition;
      max-width: max-content;
      &.active {
          color: $prm-color;
          border-bottom-color: $prm-color;
      }
    }
  }

  &__arrow {
    box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.016);
    border-radius: 2px;
    border: 1px solid #D9D9D9;
    width: 32px;
    height: 32px;
    background: #fff;

    &:after {
      content: none;
    }

    svg {
      width: 7px;
      height: 11px;
    }

    &.swiper-button-disabled {
      opacity: 0;
    }

    &-left {
      left: 0;
    }

  }
}