.filter {
  &__inner {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-right: 12px;

    @media screen and (max-width: $desctop-small) {
      margin-bottom: 24px;
      margin-right: 0;
    }
  }

  &__item {
    margin-right: 33px;
    height: 20px;

    &:last-child {
      margin-right: 0;
    }

    @media screen and (max-width: $desctop-small) {
      width: 100%;
      height: unset;
      margin-right: 0;
      margin-bottom: 27px;

      &:last-child {
        margin-bottom: 0;
      }

      .button__item {
        flex: 1;
      }

      .button--switch {
        width: 100%;
        padding-left: 0;
        padding-right: 0;
      }
    }
  }

  &__button {
    display: flex;
    align-items: center;
    background: transparent;
    border: none;
    padding: 0;

    &-text {
      display: block;
      font-size: 14px;
      margin-right: 5px;
      color: rgba(0, 0, 0, 0.65);

      @media screen and (max-width: $desctop-small) {
        font-size: 16px;
        color: rgba(0, 0, 0, 0.85);
        margin-bottom: 12px;
      }
    }

    &-icon {
      transform-origin: center;

      svg {
        display: block;

        path {
          fill: rgba(0, 0, 0, 0.45);
        }
      }
    }

    &.active {
      .filter__button-text {
        color: $prm-color;
      }

      .filter__button-icon {
        transform: rotate(180deg);

        path {
          fill: $prm-color;
        }
      }
    }

    @media screen and (max-width: $desctop-small) {
      width: 100%;

      &-icon {
        display: none;
      }

      &.active {
        .filter__button-text {
          color: rgba(0, 0, 0, 0.65);
        }
      }
    }
  }

  &__select {
    opacity: 0;
    width: 0;
    min-height: 0 !important;
    height: 0 !important;
    overflow: hidden;

    @media screen and (max-width: $desctop-small) {
      opacity: 1;
      width: 100% !important;
      min-height: unset !important;
      height: unset !important;
    }
  }

  &__dropdown {
    min-width: 208px;
    background: #FFFFFF;
    box-shadow: 0px 3px 6px -4px rgba(0, 0, 0, 0.12), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 9px 28px 8px rgba(0, 0, 0, 0.05);
    border-radius: 2px;
    border: none;

    .select2-container--default .select2-results__option--highlighted {
      background: #E3ECFF;
      color: $prm-color;
    }

    .select2-search__field:placeholder {
      color: rgba(0, 0, 0, 0.25) !important;
      font-size: 14px;
    }
  }

  &__container {
    width: 100%;

    @media screen and (max-width: $desctop-small) {
      display: none;
      position: fixed;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.28);
      align-items: center;
      justify-content: center;
      position: fixed;
      top: 0;
      left: 0;
      padding: 50px 3px;
      z-index: 20;
      overflow: auto;

      .select2-container {
        width: 100% !important;
      }
    }

    &.active {
      display: block;
    }
  }

  &__box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 23px 28px 21px 28px;
    background: #fff;
    border-radius: 6px;
    border: 1px solid #F0F0F0;

    @media screen and (max-width: $tablet) {
      justify-content: center;
    }
  }

  &__modal {
    position: absolute;
    display: none;
    background: #FFFFFF;
    box-shadow: 0px 3px 6px -4px rgba(0, 0, 0, 0.12), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 9px 28px 8px rgba(0, 0, 0, 0.05);
    border-radius: 2px;
    min-width: max-content;
    padding: 16px;
    z-index: 2;

    &.active {
      display: block;
    }

    @media screen and (max-width: $desctop-small) { 
      min-width: 100%;
      padding: 0;
      display: block;
      position: relative;
      box-shadow: none;
    }
  }

  &__button {
    &-wrap {
      display: none;
      align-items: center;

      @media screen and (max-width: $desctop-small) {
        display: flex;
      }
    }
  }

  &__reset {

    .button__close {
      display: none;
    }
    
    @media screen and (max-width: $desctop-small) {
      width: 100%;
      justify-content: space-between;
      display: flex;
      align-items: center;
      margin-bottom: 24px;

      .button__close {
        display: block;
      }
    }
  }

  &__title {
    display: none;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;

    @media screen and (max-width: $desctop-small) {
      display: block;
    }
  }

  &__form {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    &-inner {
      display: flex;
      align-items: center;

      @media screen and (max-width: $desctop-small) {
        align-items: flex-start;
        flex-direction: column;
      }
    }

    @media screen and (max-width: $desctop-small) {
      max-width: 375px;
      position: relative;
      width: 100%;
      background: #fff;
      box-shadow: 0px 3px 6px -4px rgba(0, 0, 0, 0.12), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 9px 28px 8px rgba(0, 0, 0, 0.05);
      border-radius: 2px;
      margin: auto;
      align-items: flex-start;
      flex-direction: column-reverse;
      padding: 60px 16px 16px 16px;
    }

    @media screen and (max-width: $desctop-small) {
      .button__submit-wrap {
        width: 100%;
  
        .button__submit {
          max-width: 100%;
        }
      }
    }
  }
}

.sortbox {
  &__container {
    display: none;
    width: 100%;

    @media screen and (max-width: $desctop-small) {
      display: none;
      position: fixed;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.28);
      align-items: center;
      justify-content: center;
      position: fixed;
      top: 0;
      left: 0;
      padding: 50px 3px;
      z-index: 20;
      overflow: auto;
    }

    &.active {
      display: block;
    }
  }
  &__form {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    @media screen and (max-width: $desctop-small) {
      max-width: 375px;
      position: relative;
      width: 100%;
      background: #fff;
      box-shadow: 0px 3px 6px -4px rgba(0, 0, 0, 0.12), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 9px 28px 8px rgba(0, 0, 0, 0.05);
      border-radius: 2px;
      margin: auto;
      align-items: flex-start;
      flex-direction: column;
      padding: 60px 16px 16px 16px;
    }

    .button__group  {
      width: 100%;
      margin-bottom: 24px;
    }

    .button__item {
      flex: 1;

      .button--switch {
        width: 100%;
      }
    } 

    .button__submit-wrap {
      width: 100%;

      .button__submit {
        max-width: 100%;
      }
    }
  }

  &__title {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;

    &-wrap {
      display: flex;
      width: 100%;
      justify-content: space-between;
      margin-bottom: 24px;
    }
  }

  &__box {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 12px;
  }

  &__item {
    width: calc(100%/3);
    margin-bottom: 12px;
  }
}

.range {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__item {
    display: flex;
    align-items: center;
    width: 47%;

    &-name {
      display: block;
      color: rgba(0, 0, 0, 0.65);
      margin-right: 8px;
    }
  }
}