.pagination {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__wrap {
    background: #fff;
    padding: 16px 20px;
  }

  &__select {
    width: 165px;

    @media screen and (max-width: $tablet) {
      display: none;
    }
  }

  &__list {
    display: flex;
    align-items: center;

    &-wrap {
      display: flex;
      align-items: center;
      
      @media screen and (max-width: $phone) {
        width: 100%;
        justify-content: space-between;
      }
    }
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 8px;
    width: 32px;
    height: 32px;
    padding: 0;
    border-radius: 4px;
    border: 1px solid #D9D9D9;
    background: #fff;
    color: #595959;
    transition: $transition;

    svg {
      width: 7px;
      height: 11px;
    }

    &--empty {
      border: none;
    }

    &.active {
      color: $prm-color;
      border-color: $prm-color;
    }

    &--next {
      margin-right: 0;
    }
  }
}