.report {
  &__col {
    display: flex;
    align-items: center;
    position: relative;
    top: -1px;
    &-desc {
      border-radius: 53px;
      color: #fff;
      font-weight: bold;
      font-size: 12px;
      padding: 3px 8px;
      line-height: 16px;
      position: relative;
      z-index: 1;
    }
  }

  &__item {
    font-size: 12px;
    padding: 3px 8px;
    line-height: 16px;
    border-radius: 0 53px 53px 0;
    position: relative;
    left: -12px;
    span {
      font-weight: bold;
    }
  }
}

.period {
  &__wrap {
    background: #fff;
    border-radius: 6px;
    padding: 24px;
    margin-bottom: 24px;

    @media screen and (max-width: $phone) {
      border-radius: 0;
      padding-left: 16px;
      padding-right: 16px;
  }

    &--output {
      @media screen and (max-width: $tablet) {
        .period__title {
           flex-direction: column-reverse;

           .button__report {
             max-width: max-content;
             margin-bottom: 12px;
           }
        }
      }
    }
  }

  &__table {
    &-container {
      border: 6px;
    }

    &-header {
      display: flex;
      align-items: center;
      border-bottom: 1px solid #E8E8E8;
      background: #FAFAFA;

      &-item {
        display: flex;
        align-items: center;
        flex: 1;
        padding: 16px 20px;
      }

      &-inner {
        margin-right: 10px;
        font-weight: 600;
        font-size: 14px;
      }

      @media screen and (max-width: $tablet) {
        display: none;
    }
    }
    &-body {
      background: #fff;
        &-item {
          display: flex;
          align-items: center;
          flex: 1;
          padding: 16px 20px;
          @media screen and (max-width: $tablet) {
            max-width: calc(100%/3);
            flex: auto
        }

        @media screen and (max-width: $phone) {
          padding-top: 6px;
          padding-bottom: 6px;

          &:nth-child(1) {
            order: 1;
            max-width: 50%;
            width: 100%;
          }
          &:nth-child(2) {
            order: 4;
            max-width: 50%;
            width: 100%;
          }
          &:nth-child(3) {
            order: 3;
            max-width: 100%;
            width: 100%;
          }
          &:nth-child(4) {
            order: 2;
            max-width: 50%;
            width: 100%;
          }
          &:nth-child(5) {
            order: 5;
            max-width: 50%;
            width: 100%;
          }
      }
        }

        &-inner {
          margin-right: 8px;
        }
    }

    &-row {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      border-bottom: 1px solid #E8E8E8;
      @media screen and (max-width: $tablet) {
        padding-top: 12px;
        padding-bottom: 12px;
    }
    }
  }

  &__time {
    color: #7797FD;
  }

  &__status {
    font-size: 12px;
    line-height: 20px;
    border: 1px solid;
    padding: 1px 8px;
    border-radius: 2px;

    &.green {
      color: #52C41A;
      background: #F6FFED;
      border-color: #B7EB8F;
    }

    &.purple {
      color: #722ED1;
      background: #F9F0FF;
      border-color: #D3ADF7;
    }

    &.orange {
      color: #FA8C16;
      background: #FFF7E6;
      border-color: #FFD591;
    }

    &.red {
      color: #F5222D;
      background: #FFF1F0;
      border-color: #FFA39E;
    }
  }

  &__title {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
  }
}