.menu {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-left: 0;
    height: 100vh;
    background: #fff;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
    top: 0;
    left: 0;
    margin: 0;
    z-index: 10;
    max-width: 50px;
    overflow: hidden;
    cursor: pointer;
    transition-delay: 30s;
    transition: max-width $transition;

    &__inner {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
        width: 270px;
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 16px;
        max-width: 50px;

        &-name {
            transition: $transition;
            opacity: 0;
            white-space: nowrap;
        }

        &:hover {
            background: #E8EFFF;
            box-shadow: inset -3px 0px 0px $prm-color;
            color: $prm-color;

            .action-icon {
                fill: $prm-color;
            }

            .menu__item-name {
                color: $prm-color;
            }
        }
    }

    &__link {
        color: $main-color;
        display: flex;
        align-items: center;
        transition: $transition;
    }

    &__icon {
        position: relative;
        width: 16px;
        height: 16px;
        margin-right: 10px;

        svg {
            display: block;
            width: 100%;
            height: 100%;

            .action-icon path {
                transition: $transition;
            }
        }

        &--question {
            width: 20px;
            height: 20px;
            margin-right: 0;
        }

        &-box {
            display: flex;
            align-items: center;
            justify-content: center;
            background: $light-grey-color;
            border-radius: 50%;
            min-width: 32px;
            height: 32px;
            // margin-right: 16px;
        }

        &--exist:after {
            content: '';
            display: block;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background: #FF4D4F;
            position: absolute;
            top: 0;
            right: 0;
        }
    }

    &__box {
        &-item {
            display: flex;
            padding-left: 8px;
            padding-right: 14px;

            &--question {
                padding-top: 24px;
                border-top:  1px solid rgba($color: #000000, $alpha: .1);
                padding-bottom: 16px;
            }

            &--contact {
                border-bottom:  1px solid rgba($color: #000000, $alpha: .1);
                opacity: 0;
                height: 0;
                overflow: hidden;
            }

            &--setting {
                padding-top: 8px;
                padding-bottom: 8px;
            }
        }

        &-info {
            display: flex;
            flex-direction: column;
            opacity: 0;
            height: 0;
            overflow: hidden;
        }

        &-title {
            font-weight: 500;
            font-size: 14px;
        }

        &-desc {
            color: rgba(0, 0, 0, 0.45);
            font-size: 12px;
            line-height: 140%;
        }

        &-link {
            display: flex;
            flex-direction: column;
        }
    }

    &__avatar {
        min-width: 32px;
        height: 32px;
        border-radius: 50%;
        overflow: hidden;
        // margin-right: 16px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &--contact {
            height: 0;
        }
    }

    &__contact {
        font-size: 14px;
        color: $prm-color;
    }

    &__setting {
        display: flex;
        align-items: center;
        color: $main-color;
        font-weight: 500;
        display: none; 

        &-box {
            display: flex;
            align-items: center;
        }

        &:hover {
            color: $main-color;
        }
    }

    &__header {
        position: relative;
        padding: 12px;
    }

    &__logo {
        padding-right: 5px;

        &-wrap {
            display: flex;
            align-items: center;
            opacity: 0;
        }

        &-text {
            font-size: 10px;
            display: block;
            padding-left: 6px;
            border-left: 1px solid rgba($color: #000000, $alpha: .1);
            font-weight: 500;
        }
    }

    &__close {
        position: absolute;
        top: 0;
        bottom: 0;
        display: flex;
        align-items: center;

        &-inner {
            width: 15px;
            height: 13px;
            margin-left: 5px;
            
            svg {
                width: 100%;
                height: 100%;
            }
        }
    }

    &:hover {
        max-width: 270px;

        .menu__item-name {
            opacity: 1;
        }

        .menu__logo-wrap {
            opacity: 1;
        }

        .menu__close {
            display: none;
        }

        .menu__item {
            max-width: 100%;
        }

        .menu__box-info {
            opacity: 1;
            height: unset;
        }

        .menu__box-item--contact {
            opacity: 1;
            height: unset;
        }

        .notification__item, .menu__setting {
            display: flex;
        }

        .menu__icon-box, .menu__avatar {
            margin-right: 16px;
        }

        .menu__avatar--contact {
            height: 32px;
        }

        .menu__box-item--contact {
            padding-bottom: 24px;
        }           
    }

    @media screen and (max-width: $tablet) {
        display: none;
    }

    &.active {
        display: block;
        .menu__item-name {
            opacity: 1;
        }

        .menu__logo-wrap {
            opacity: 1;
        }

        .menu__close {
            display: none;
        }

        .menu__item {
            max-width: 100%;
        }

        .menu__box-info {
            opacity: 1;
            height: unset;
        }

        .menu__box-item--contact {
            opacity: 1;
            height: unset;
        }

        .notification__item, .menu__setting {
            display: flex;
        }

        .menu__icon-box, .menu__avatar {
            margin-right: 16px;
        }

        .menu__avatar--contact {
            height: 32px;
        }

        .menu__box-item--contact {
            padding-bottom: 24px;
        }     
    }

    @media screen and (max-width: $tablet) {
        max-width: 100%;
        width: 100%;
        background: rgba(0, 0, 0, 0.28);

        &:hover {
            max-width: 100%;
            width: 100%;
        }

        &__inner {
            max-width: 270px;
            background: #fff;
        }

        .menu__box-link, .menu__box-item--question .menu__box-desc {
            display: none;
        }
    }
}

.notification {
    &__item {
        display: none;
        color: #FFF;
        font-size: 11px;
        background: $red-color;
        border-radius: 10px;
        padding: 1px 6px;
        line-height: 16px;
    }
}